import React, { Component } from 'react'
import { Table } from 'antd'
import { observer, inject } from 'mobx-react'

let data = []
const columns = [
    {
        title: '姓名',
        key: 'name',
        dataIndex: 'name'
    },
    {
        title: '序号',
        key: 'number',
        dataIndex: 'number'
    },
    {
        title: '部门',
        key: 'tags',
        dataIndex: 'tags'
    },
    {
        title: '职位',
        key: 'action',
        dataIndex: 'action'
    },
    {
        title: '负责班级',
        key: 'chargeClass',
        dataIndex: 'chargeClass'
    },
    {
        title: '幼儿园',
        key: 'kindergarten',
        dataIndex: 'kindergarten'
    },
    {
        title: "地址",
        key: 'address',
        dataIndex: "address"
    },
    {
        title: "手机号",
        key: 'phone',
        dataIndex: "phone"
    },
    {
        title: '总课时（h）',
        key: 'totalHours',
        dataIndex: 'totalHours'
    }
];

@inject('ContorlData')
@observer
class TableBox extends Component {
    state = {
        keyArr: [],
        ind: 0
    }
    static defaultProps = {
        columns,
        data,
        rowSelectionOpen: false
    }
    render() {
        let { columns, data, rowSelectionOpen } = this.props
        let { ind } = this.state
        let { staffList, staffLength } = this.props.ContorlData
        data = [];
        staffList.data && staffList.data.forEach((item, index) => {
            let obj = {
                name: item.staffName,
                number: index + 1 + (ind * 10),
                tags: item.department,
                action: item.position === "1" ? "主管" : (item.position === "2" ? "讲师" : item.position),
                chargeClass: item.classNum,
                kindergarten: item.gartenId,
                totalHours: item.classNum,
                address: item.address,
                phone: item.tel,
                key: item.id ? item.id : index
            }
            data.push(obj)
        })
        const rowSelection = {
            selectedRowKeys: this.props.ContorlData.selectedRowKeys,
            onChange: (selectedRowKeys) => {
                this.props.ContorlData.selectedRowKeys = selectedRowKeys;
            },
            onSelect: (record, selected) => {
                let { keyArr } = this.state
                if (selected && !(keyArr.includes(record.key))) {
                    keyArr.push(record.key)
                } else {
                    let ind = keyArr.findIndex(item => item == record.key)
                    keyArr.splice(ind, 1)
                }
                this.setState({
                    keyArr
                })
                this.props.keyArrFn(keyArr)
            },
            onSelectAll: (selected, selectedRows) => {
                let { keyArr } = this.state
                if (selected) {
                    selectedRows.forEach(item => {
                        if (!(keyArr.includes(item.key))) {
                            keyArr.push(item.key)
                        }
                    })
                } else {
                    keyArr = []
                }
                this.setState({
                    keyArr
                })
                this.props.keyArrFn(keyArr)
            }
        }
        return (
            <>
                {
                    rowSelectionOpen ? <Table columns={columns} pagination={{ total: staffLength, onChange: this.changeFn }} rowSelection={rowSelection} dataSource={data} /> : <Table columns={columns} pagination={{ total: staffLength, onChange: this.changeFn }} dataSource={data} />
                }
            </>
        )
    }
    changeFn = (ind) => {
        let pageid = ind - 1
        this.setState({
            ind: pageid
        })
        this.props.ContorlData.getStaffListFn(10, pageid)
    }
    componentDidMount() {

    }
}

export default TableBox;
