import React, { Component } from 'react'
import './style/contorl.scss'
import { Input, Button, Icon, Upload, message, Modal } from 'antd';
import pageList from '../../../router/children/controlRouter.js'
import { original, pushData, exportXlsx } from "@/api/"
import TableBox from "@/components/table/index"
import { observer, inject } from 'mobx-react'
import Dialog from "@/components/dialog";

const { Search } = Input;
const { confirm } = Modal
@inject('ContorlData')
@observer
class Control extends Component {
    state = {
        tpyeTitle: '测试页面',
        limit: 10,
        pageid: 0,
        pathName: '',
        keyArr: [],
        title: "确定删除吗？",
        open:false,
        item:{}
    }
    keyArrFn = (keyArr) => {
        this.setState({
            keyArr
        })
    }
    render() {
        let { tpyeTitle , open} = this.state;
        return (
            <div className="contorl">
                <div className="headerBox">
                    {tpyeTitle}
                </div>
                <div className="iptBox">
                    <Search placeholder="输入姓名或手机号码查询" enterButton />
                </div>
                <div className="buttons">
                    <Button type="primary" onClick={this.add.bind(this)}><Icon type="form" />新增</Button>
                    <Button type="danger" onClick={this.showConfirmDel.bind(this)}><Icon type="delete" />删除</Button>
                    <Upload customRequest={this.pushxlsx} showUploadList={false} >
                        <Button style={{ background: "orange", color: "#fff" }}  ><Icon type="download" />导入</Button>
                    </Upload>
                    <Button style={{ background: "red", color: "#fff" }} onClick={this.exportXlsxFn.bind(this)} ><Icon type="to-top" />导出</Button>
                    <Button style={{ background: "red", color: "#fff" }} onClick={() => { this.downloadOrigin() }}><Icon type="to-top" />下载模板</Button>
                </div>
                <div className="tableBox">
                    <TableBox rowSelectionOpen={true} keyArrFn={this.keyArrFn} />
                </div>
                <Dialog  open={open} item={this.state.item} onDialog={this.dialog.bind(this)} edited={[]}/>
            </div>
        )
    }
    add(){
        this.setState({
            open:true,
            item:{}
          })
    }
    dialog(open){
        this.setState({
            open:!open
          })
        }
    exportXlsxFn() {
        this.commonFn('export')
    }
    showConfirmDel() {
        this.commonFn('delete')
    }
    commonFn(content) {
        let { keyArr } = this.state
        let { staffList } = this.props.ContorlData
        let { staffListOfDel } = this.props.ContorlData
        if (keyArr.length) {
            let that = this;
            confirm({
                title: content === "delete" ? "确定删除吗？" : "确定导出吗？",
                onOk() {
                    if (content === "delete") {
                        return new Promise((resolve) => {
                            staffListOfDel(keyArr)
                            resolve()
                        }).then(() => {
                            keyArr.forEach(item => {
                                let ind = staffList.data.findIndex((ele) => ele.id === item)
                                let indArr = []
                                indArr.push(ind)
                                staffList.data.splice(ind, indArr.length)
                            })
                            that.setState({
                                keyArr: []
                            })
                            // console.log(`删除成功`)  
                        })
                    } else if (content === "export") {
                        exportXlsx({ data: JSON.stringify(keyArr) }).then(res => {
                            const aDom = document.createElement('a');
                            aDom.href = window.URL.createObjectURL(new Blob([res]));
                            aDom.setAttribute('download', 'staffList.xlsx')
                            aDom.style.display = 'none';
                            document.body.appendChild(aDom);
                            aDom.click();
                            document.body.removeChild(aDom)
                        })
                        that.setState({
                            keyArr: []
                        })
                        that.props.ContorlData.selectedRowKeys = []
                    }
                }
            });
        } else {
            message.warning('至少选择一项进行操作,小可爱^_^');
        }

    }
    pushxlsx = (event) => {
        let { file } = event;
        let FileData = new FormData();
        FileData.append('file', file);
        pushData(FileData).then(res => {
            if(res.code){
                this.props.ContorlData.getStaffListFn()
                message.success('导入成功 ^_^')
            }
        })
    }
    downloadOrigin = () => {
        original({
            filename: "staffList"
        }).then(res => {
            const aDom = document.createElement('a');
            aDom.href = window.URL.createObjectURL(new Blob([res]));
            aDom.setAttribute('download', 'staffList.xlsx')
            aDom.style.display = 'none';
            document.body.appendChild(aDom);
            aDom.click();
            document.body.removeChild(aDom)
        })
    }
    getTitle() {
        let { title } = pageList.find(item => item.path === this.props.location.pathname).meta;
        this.setState({ tpyeTitle: title })
    }
    componentDidMount() {
        this.getTitle();
        let { limit, pageid } = this.state
        this.props.ContorlData.getStaffListFn(limit, pageid)
    }
}

export default Control;